
import Toast from 'vux/src/components/toast/index.vue'

import {SubmitWhereKnowUrl} from '@/api/api'
export default{
data(){
return{
InputContent:'',
ToastShow:false,
ToastTest:'',
Area:'请选择您所属的地区',
SelectList:[
{name:'请选择您所属的地区'},
{name:'南湖'},
{name:'秀洲'},
{name:'嘉善'},
{name:'平湖'},
{name:'海盐'},
{name:'海宁'},
{name:'桐乡'},
{name:'经开'},
{name:'港区'},
{name:'出入境管理局'}
]
}
},
created(){

},
methods:{
CheckInfo(){
if (this.Area=='请选择您所属的地区') {
this.ToastTest="请选择您所属的地区"
this.ToastShow=true
return false
}
if (this.InputContent=='') {
this.ToastTest="请填写完整信息"
this.ToastShow=true
return false
}
return true
},
SubmitAjax(){
if (!this.CheckInfo()) {
return
}
this.$http.post(SubmitWhereKnowUrl,{area:this.Area,content:this.InputContent}).then((res)=>{

this.ToastTest="提交成功"
this.ToastShow=true
})
}
},
components:{
Toast
}
}



